import Vue from 'vue'
import VueRouter from 'vue-router'

// 导入对应的路由组件
import HomeContainer from '@/components/tabbar/HomeContainer'

Vue.use(VueRouter)

// 暴漏路由对象给 main.js的
export default new VueRouter({
  routes: [
    {path : '/',redirect:'/home' },
    { path : '/home' , component: HomeContainer}
  ]
})



